.menu {
  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
  }

  &__link {
    color: #fff;
    text-decoration: none;
    font-family: Poppins, sans-serif;
    font-weight: bold;
    font-size: 13px;
    line-height: 19px;
    letter-spacing: 3px;
    text-transform: uppercase;
    transition: color 0.3s ease;

    &:hover {
      color: $blue;
    }
  }
}